<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png">
    <ul>
      <li v-for="review in reviews" :key="review._id">
        <h3>{{review.txt}}</h3>
        <p>
          About
          <router-link :to="`user/${review.aboutUser._id}`">
            {{review.aboutUser.username}}
          </router-link> 
        </p>
        <p>By 
          <router-link :to="`user/${review.byUser._id}`">
            {{review.byUser.username}}
          </router-link>
        </p>
        <hr />
      </li>
    </ul>
    <hr />
    <form @submit.prevent="addReview()">
      <select v-model="reviewToEdit.aboutUserId">
        <option v-for="user in users" :key="user._id" :value="user._id" >
          {{user.username}}
        </option>
      </select>
      <textarea placeholder="Your Opinion Matters..." v-model="reviewToEdit.txt"></textarea>
      <button>Save</button>
    </form>
    

  </div>

  
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      reviewToEdit: {
        txt: '',
        aboutUserId: null
      }
    }
  },
  computed: {
    reviews() {
      return this.$store.getters.reviews;
    },
    users() {
      return this.$store.getters.users
    },
  },
  created() {
    this.$store.dispatch({type: 'loadUsers'})
    this.$store.dispatch({type: 'loadReviews'})
  },
  methods: {
    addReview() {
      this.$store.dispatch({type: 'addReview', review: this.reviewToEdit})
      this.reviewToEdit = {txt: '', aboutUserId: null}
    }
  }

  
}
</script>
